<template>
    <div class="parent">
        <h2>DashBoard子組件</h2>

        <ul>
            <li>person: {{ person }}</li>
            <li>姓名: {{ name }} ( {{ age }} )</li>
            <li>car: {{ car }}</li>
            
        </ul>
        
        <button @click="getChildRef">取得Child組件的childRef元素</button>


        <!-- 父組件透過 v-model 向子組件傳遞元素  -->
        <!-- https://blog.csdn.net/pig_ning/article/details/135205597 -->
        
        <TheChild ref="parentRef"
            v-model="name"
            :age="age" @eventUpdateAge="updateAge"
            @eventUpdateHobby="updateHobby"
            :car="car" @eventAddCar="addCar"
        />
    </div>
    

</template>

<script>
import { ref, reactive, provide, onMounted } from 'vue'
import TheChild from './TheChild.vue'

export default {
    name: 'DashBoard',
    components: { TheChild },

    setup() {
        // 獲取ref元素
        // https://stackoverflow.com/questions/61600078/vue-3-composition-api-how-to-get-the-component-element-el-on-which-the-comp
        let parentRef = ref(null)

        // 數據
        let person = reactive({
            name: '李四',
            age: 38,
            hobby: ["看電視", "睡覺"]
        })

        let name = ref('張三')
        let age = ref(18)
        
        let car = reactive([
            {
                name: '奔馳',
                price: 40
            }
        ])


        // 方法

        // 更新年齡
        function updateAge(value) {
            age.value = value
        }

        // 更新興趣
        function updateHobby() {
            person.hobby[0]+='~'
        }

        // 新增一台車
        function addCar(obj) {
            car.push(obj)
        }

        // 取得 ref 的 ref
        function getChildRef() {
            console.log('來自子組件的ref', parentRef.value.childRef)
        }


        // 提供數據給後代元素
        provide('person', person)



        onMounted(() => {
            // the DOM element will be assigned to the ref after initial render
            console.log(parentRef.value) // this is your $el
        })


        return {
            parentRef,

            person,
            name,
            age,
            car,

            updateAge,
            updateHobby,
            addCar,

            getChildRef,
        }
    }
}

</script>
<style lang="scss">

.parent {
    padding: 20px;
    background-color: lightgray;

    .child {
        padding: 20px;
        background-color: orange;

        .son {
        padding: 20px;
        background-color: skyblue;
        }
    }
}


</style>