<template>
    <div class="child">
        <h3 ref="childRef">Child子組件</h3>

        <ul>
            <li>姓名: <input type="text" :value="modelValue" @input="$emit('update:modelValue', $event.target.value)"></li>
            <li>年齡: <input type="number" v-model="initAge" @change="updateAge"></li>
        </ul>

        <TheSon 
            @eventUpdateHobby="updateHobby"
            :car="initCar" 
            @eventAddCar="addCar"
        />

    </div>
</template>

<script>
import { ref, toRef } from 'vue'
import TheSon from './TheSon.vue'

export default {
    name: 'TheChild',
    components: {
        TheSon
    },

    props: ['modelValue', 'age', 'car'],
    emits: ['update:modelValue', 'eventUpdateAge', 'eventUpdateHobby', 'eventAddCar'],


    setup(props,context) {

        // 元素
        let childRef = ref(null)

        // 來自父組件的數據
        console.log('props', props)

        let initAge = toRef(props.age);
        let initCar = toRef(props, 'car')

        function updateAge() {
            // console.log('initAge.value', initAge.value)
            context.emit('eventUpdateAge', initAge.value)
        }

        function updateHobby() {
            context.emit('eventUpdateHobby')  // 觸發來自 DashBoard子組件 的自定義事件
        }

        function addCar(obj) {
            context.emit('eventAddCar', obj) // 觸發來自 DashBoard子組件 的自定義事件
        }


        return {
            childRef,

            initAge,
            initCar,

            updateAge,
            updateHobby,
            addCar,
        }
    }
}

</script>