<template>

    <div class="son">
        <h4>Son子組件</h4>

        <ul>
            <li>{{ person }}</li>
            <li>{{ initCar }}</li>
        </ul>

        
        <button @click="updateHobby">修改興趣</button>
        <button @click="addCar">新增一台車</button>
    </div>
    
</template>

<script>
import { inject, toRef } from 'vue'

export default {
    name: 'TheSon',
    props: ['car'],
    emits: ['eventUpdateHobby', 'eventAddCar'],

    setup(props, context) {
        // 接收組先提供的數據
        let person = inject('person')

        let initCar = toRef(props, 'car')


        function updateHobby() {
            context.emit('eventUpdateHobby')  // 觸發來自 Child子組件 的自定義事件
        }

        function addCar() {
            let obj = {name: '法拉利', price: 100}
            context.emit('eventAddCar', obj) // 觸發來自 DashBoard子組件 的自定義事件
        }


        return {
            person,
            initCar,

            updateHobby,
            addCar,
        }
    }
}
</script>